<template>
  <div class="cms-list-layout">
    <cms-tabs>
      <cms-tab to="/recharge/recharge">充值</cms-tab>
      <cms-tab to="/recharge/present">提现</cms-tab>
    </cms-tabs>
    <cms-prompt collapse="true">
      <li>显示所有会员充值记录及提现申请</li>
      <li>可以对充值记录更改状态及提现申请审核</li>
    </cms-prompt>
    <section class="cms-list-query">
      <div class="query-group">
        <label>会员账号</label>
        <el-input v-model="queryParams.account" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <label>状态</label>
        <el-select v-model="queryParams.status" @change="query">
          <el-option label="全部" value></el-option>
          <el-option label="待审核" :value="'1'"></el-option>
          <el-option label="审核不通过" :value="'2'"></el-option>
          <el-option label="转账失败" :value="'4'"></el-option>
          <el-option label="完成" :value="'3'"></el-option>
        </el-select>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker="true" @keyup.native.enter="query">查询</cms-button>
      </div>
    </section>
    <el-table
      :data="tableData"
      style="width: 100%"
      stripe
      v-loading="loading"
      @selection-change="getCheckItems"
    >
      <el-table-column prop="id" label="id" width="60"></el-table-column>
      <el-table-column prop="businessNo" label="提现编号" min-width="200"></el-table-column>
      <el-table-column prop="member.username" label="会员账户" min-width="110" align="center"></el-table-column>
      <el-table-column prop="money" label="提现金额" min-width="100" align="center">
        <template slot-scope="scope">{{$cms.float(scope.row.money)}}</template>
      </el-table-column>
      <el-table-column prop label="账户类型" min-width="130" align="center">
        <template slot-scope="scope">
          <span v-if="scope.row.accountTypeCode == '1'">支付宝</span>
          <span v-else-if="scope.row.accountTypeCode == '2'">微信</span>
          <span v-else-if="scope.row.accountTypeCode == '3'">银联</span>
          <span v-else-if="scope.row.accountTypeCode == '4'">余额</span>
        </template>
      </el-table-column>
      <el-table-column prop="account" label="账户" min-width="150" align="center"></el-table-column>
      <el-table-column prop="status" label="状态" min-width="100" align="center">
        <div slot-scope="scope">
          <span v-if="scope.row.status === 1" class="info">待审核</span>
          <span v-else-if="scope.row.status === 2" class="error">未完成</span>
          <span v-else-if="scope.row.status === 3" class="success">已完成</span>
          <span v-else-if="scope.row.status === 4" class="error">转账失败</span>
        </div>
      </el-table-column>
      <el-table-column prop="tradeNo" label="交易流水号" min-width="300" align="center"></el-table-column>
      <el-table-column prop="createTime" label="申请时间" min-width="170" align="center">
        <template slot-scope="scope">
          {{$moment(scope.row.createTime)}}
        </template>
      </el-table-column>
      <el-table-column prop="finishTime" label="完成转账时间" min-width="170" align="center">
        <template slot-scope="scope">
          {{$moment(scope.row.finishTime)}}
        </template>
      </el-table-column>
      <el-table-column prop="remark" label="备注" min-width="240"></el-table-column>
      <el-table-column label="操作选项" width="210" fixed="right" align="right">
        <div slot-scope="scope">
          <cms-button
            icon="edit"
            size="small"
            type="warning"
            v-if="scope.row.status === 1"
            @click="examine(scope.row.id)"
          >审核</cms-button>
          <cms-button
            icon="error"
            size="small"
            type="info"
            v-if="scope.row.status === 1"
            @click="examineDis(scope.row.id)"
          >审核不通过</cms-button>
          <cms-button
            v-if="scope.row.status === 4"
            icon="error"
            size="small"
            type="info"
            @click="deleteData(scope.row.id)"
          >删除</cms-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total="total"></cms-pagination>
  </div>
</template>

<script>
import {
  presentList,
  presentDelete,
  presentPass,
  presentReject
} from '@/api/users/recharge.js'
import list from '@/mixins/list'
var defaultData = {
  username: '',
  status: '',
  accountTypeCode: '',
  page: 1,
  size: 10
}
export default {
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData),
      value: ''
    }
  },
  methods: {
    examine(id) {
      this.$confirm(
        '提现申请通过后，系统将自动调用支付插件中设置的支付宝账户进行转账，请谨慎操作',
        '系统提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      )
        .then(() => {
          presentPass({ ids: [id] }).then(res => {
            if (res.code === 200) {
              this.$successMessage('审核成功')
              this.getTableData(presentList, this.queryParams)
            } else {
              this.$errorMessage(res.message)
            }
          })
        })
        .catch(() => {})
    },
    examineDis(id) {
      this.$prompt('原因', '系统提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputPlaceholder: '请输入100字以内不通过原因',
        inputType: 'textarea',
        inputPattern: /^.{1,100}$/,
        inputErrorMessage: '请输入100字以内不通过原因'
      })
        .then(({ value }) => {
          presentReject({ ids: [id], data: value }).then(res => {
            if (res.code === 200) {
              this.$successMessage('操作成功')
              this.getTableData(presentList, this.queryParams)
            } else {
              this.$errorMessage(res.message)
            }
          })
        })
        .catch(() => {})
    },
    deleteData(id) {
      // 包装一层删除的操作
      this.deleteTableData(presentDelete, id) // 这个才是真实的删除方法
    },
    deleteDatas(ids) {
      this.deleteTableDatas(presentDelete, ids) // 这个才是真实的删除方法
    },
    getTableData(url, params, isPage = true) {
      // 获取表格数据,处理翻页选中
      this.tableData = []
      this.loading = true
      this.callback = url
      this.isPage = isPage
      setTimeout(() => {
        url(params)
          .then(res => {
            if (res.code === 200) {
              if (isPage) {
                this.total = res.data.totalElements
                this.tableData = res.data.content
              } else {
                this.tableData = res.data.content
              }
            } else {
              this.$errorMessage(res.message)
            }
            this.loading = false
          })
          .catch(res => {
            this.loading = false
          })
      }, 1)
    }
  },
  created() {
    this.getTableData(presentList, this.queryParams)
  }
}
</script>

<style>
</style>



